import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import connect from 'connect'
import {withRouter, Redirect} from 'react-router-dom'
import { Modal, Button, WhiteSpace, WingBlank, NavBar, List, Grid, Card, Flex, Stepper, Toast } from 'antd-mobile';
const alert = Modal.alert;
@connect
@withRouter
class Home extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
          navTop: false,
          swiper: null,
          value: '美食',
          data: ['1', '2', '3'],
          imgHeight: 176,
           disabled: false,
            val: 2,
            modal3: false,
            modal2: false
        }

        this.$tab = null;
        this.offsetTop = 0;
    }

    
    componentDidMount () {
       setTimeout(() => {
             this.setState({
               data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
             });
           }, 100);
    }


      onChange= (value) => {
        this.setState({ value });
      };
      clear = () => {
        this.setState({ value: '' });
      };
      handleClick = () => {
        this.manualFocusInst.focus();
    }

    handleScroll(){
        let sTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;

        if(!this.state.navTop && sTop >= this.offsetTop){
           this.setState({
             navTop: true
           })
        }

        if(sTop < this.offsetTop){
           this.setState({
             navTop:false
           })
        }
      }

    renderTabBar(props) {
      return (<div>
        {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
      </div>);
    }

    concat() {
      alert('温馨提示', '18224528561', [
        { text: '取消', onPress: () => {console.log('cancel')} },
        { text: '呼叫', onPress: () => {window.location.href = "tel:16638139714";} },
      ])
    }

    showModal = key => (e) => {
        e.preventDefault(); // 修复 Android 上点击穿透
        this.setState({
          [key]: true,
        });
      }
    onClose = key => () => {
      this.setState({
        [key]: false,
      });
    }

     onChange2 = (val) => {
          console.log(val);
         this.setState({ val });
    }

    gouwuche() {
      this.setState({
        modal2: false,
      });
      Toast.info('成功加入购物车', 2, null, false);

    }

    buy() {
      this.setState({
        modal3: false,
      });
      this.props.history.slideStatus = 'left'
      this.props.history.push(`/setorder`)
    }



    render () {
      const tabs = [
        { title: '全部' },
        { title: '山水画' },
        { title: '花鸟画' },
        { title: '花鸟画' },
        { title: '山水画' },
        { title: '花鸟画' },
        { title: '花鸟画' }
      ];
      const data = [
        {
          img: require('../../images/lian.png'),
          title: 'Meet hotel',
          des: '名称名称名称名称名称名称名称名称名称名称名称名称',
          value: 0,
        },
      ];
        const Item = List.Item;
        const Brief = Item.Brief;
        return (
            <div className="gooddetail-container">
            <NavBar
                 mode="light"
                 icon={<img src={require('../../images/back.png')} alt="LOGO" className="back"  onClick={this.props.history.goBack}/>}
               >详情</NavBar>
               <Card full={true}>
                     <Card.Header
                       thumb={require('../../images/lian.png')}
                       thumbStyle={{width: '3.6rem',height: '2.03rem'}}
                     />
                     <Card.Body>
                       <div>名称名称名称名称名称名称名称名称名称</div>
                     </Card.Body>
                     <Card.Footer content={<div>
                      <span  className="hongtext">￥2000.00</span>
                      <span className="futext">今日价</span>
                      <span className="collectjia">收藏价：90000.00</span>
                      <div><span className="yunfei">运费：￥10.00</span></div>
                      </div>} />
                   </Card>
            <List>
                <Item multipleLine>
                    <div  className="itemtitle">作品信息</div>
                    <div  className="itemin">
                      <p className="intros"><span className="introtitle">【作品名称】</span>《名称名称名称名称名称》</p>
                      <p className="intros"><span className="introtitle">【作者】</span>张三</p>
                      <p className="intros"><span className="introtitle">【尺寸】</span>60*90cm</p>
                      <p className="intros"><span className="introtitle">【材质】</span>宣纸</p>
                      <p className="intros"><span className="introtitle">【类别】</span>国画</p>
                      <p className="intros"><span className="introtitle">【题材】</span>花鸟</p>
                      <p className="intros"><span className="introtitle">【创作年代】</span>1998-07</p>
                      <p className="intros"><span className="introtitle">【装裱形式】</span>未装裱</p>
                    </div>
                    
                </Item>
                <Item multipleLine>
                    <div  className="itemtitle">作者介绍</div>
                    <p className="intro">张三出生于1890年是中国书法家协会委员张三出生于1890年是中国书法家协会委员张三出生于1890年是中国书法家协会委员张三出生于1890年是中国书法家协会委员张三出生于1890年是中国书法家协会委员</p>
                </Item>
                <Item multipleLine>
                    <div  className="itemtitle">作品细节</div>
                    <div  className="itemin">
                      <img src={require('../../images/lian.png')} alt="LOGO" className="desimg" />
                      <img src={require('../../images/shuhua.png')} alt="LOGO" className="desimg" />
                      <img src={require('../../images/lian.png')} alt="LOGO" className="desimg" />
                      <img src={require('../../images/shuhua.png')} alt="LOGO" className="desimg" />
                    </div>
                </Item>
                <Item multipleLine>
                    <div  className="itemtitle">买家评价</div>
                    <div  className="pinbox">
                      <span className="noping">暂时还没有评论</span>
                    </div>
                </Item>

             </List>
             <Item key="all" data-seed="logId" className="allcell" >
                 <div style={{ display: '-webkit-box', display: 'flex'}}>
                 <div className="xycenter">
                       <img src={require('../../images/sp_wsc.png')} alt="LOGO" className="sc" />
                       <div className="bottomall">收藏</div>
                  </div>
                <div className="xycenter" onClick={() => {this.concat()}}>
                      <img src={require('../../images/sp_lx.png')} alt="LOGO" className="sc" />
                      <div className="bottomall">联系</div>
                 </div>
                 <Button type="warning" onClick={this.showModal('modal2')}>加入购物车</Button>
                 <Button type="warning" className="goumai"  onClick={this.showModal('modal3')}>立即购买</Button>
               </div>
             </Item>
             <Modal
                       popup
                       visible={this.state.modal2}
                       onClose={this.onClose('modal2')}
                       animationType="slide-up"
                     >
                        {data.map((obj, index) => (
                         <div  key={'myordercard' + index} >
                          <Card full={true}>
                                <Card.Header
                                  thumb={obj.img}                                  
                                  thumbStyle={{width: '1rem',height: '0.75rem'}}
                                  extra={
                                   <div style={{ }}>
                                     <div className="destext">{obj.des}</div>
                                     <div className="mid"></div>
                                     <div><span className="jiatext">¥9900.00</span><span className="numtextdes">库存1件</span></div>
                                   </div>
                                  }
                                />
                                <Card.Body>
                                  <div><span className="numtextdes">数量</span>
                                  <Stepper
                                    style={{ width: '0.96rem' }}
                                    showNumber
                                    max={10}
                                    min={1}
                                    value={this.state.val}
                                    onChange={this.onChange2}
                                  />
                                  </div>
                                </Card.Body>
                                <Card.Footer content={<div className="cardfooter">
                                       <Button inline style={{ marginRight: '4px' }} className="am-button-borderfix" onClick={() => {this.gouwuche()}}>加入购物车</Button>
                                </div>} />
                          </Card>
                       </div>
                        ))}
                     </Modal>
                <Modal
                          popup
                          visible={this.state.modal3}
                          onClose={this.onClose('modal3')}
                          animationType="slide-up"
                        >
                           {data.map((obj, index) => (
                            <div  key={'myordercard' + index} >
                             <Card full={true}>
                                   <Card.Header
                                     thumb={obj.img}                                  
                                     thumbStyle={{width: '1rem',height: '0.75rem'}}
                                     extra={
                                      <div style={{ }}>
                                        <div className="destext">{obj.des}</div>
                                        <div className="mid"></div>
                                        <div><span className="jiatext">¥9900.00</span><span className="numtextdes">库存1件</span></div>
                                      </div>
                                     }
                                   />
                                   <Card.Body>
                                     <div><span className="numtextdes">数量</span>
                                     <Stepper
                                       style={{ width: '0.96rem' }}
                                       showNumber
                                       max={10}
                                       min={1}
                                       value={this.state.val}
                                       onChange={this.onChange2}
                                     />
                                     </div>
                                   </Card.Body>
                                   <Card.Footer content={<div className="cardfooter">
                                          <Button inline style={{ marginRight: '4px' }} className="am-button-borderfix"  onClick={() => {this.buy()}}>确定</Button>
                                   </div>} />
                             </Card>
                          </div>
                           ))}
                        </Modal>
            
     </div>
        )
    }
}
export default Home


